import React, { useState } from 'react';

const AddCommentForm = () => {
const [username, setUsername] = useState('');
const [commentText, setCommentText] = useState('');

    const addComment = async ({ articleName, setArticleInfo }) => {
        const result = await fetch(`/api/articles/${articleName}/add-comment`, {
            method: 'post',
            body: JSON.stringify({username, text: commentText}),
            headers: {
                'Content-type': 'application/json',
            }
        });
        const body = await result.json();
        setArticleInfo(body);
        setUsername('');
        setCommentText('');
    }
    
return (
    <div id="add-comment-form">
    <label htmlFor="name">Name:
    <input id="name" text="text" value={username} onChange={(event) => setUsername(event.target.value)}/>
    </label>
    <label htmlFor="comment">Comment:
    <textarea id="comment" rows="4" cols="50" value={commentText} onChange={(event) => setCommentText(event.target.value)} />
    </label>
    <button onClick={()=> addComment()}>Add Comment</button>
    </div>
);
}
export default AddCommentForm;